/** @jsx jsx */
import { jsx } from "theme-ui"
import React from "react"

export default function Header_001(props) {
  return (
    <>
      <h1
        // {...props}
        sx={{
          color: "primary",
          "@media (prefers-color-scheme: dark)": {
            color: "secondary",
          },
        }}
      >
        {props.headerText}
      </h1>
    </>
  )
}
